import fetch from 'isomorphic-fetch';
import {getAPI, getBaseUrl} from "../utils/Api";
import {getRandomCode} from "../utils";
import merge from 'lodash/merge'

import {REQUEST_POSTS,
        RECEIVE_POSTS,
        SHOW_ERROR,
        RESET,
        REQUEST_FORM,
        RECEIVE_FORM,
        CHANGE_TYPE,
        SELECT_PAY_TYPE_LOG,
        SELECT_PAY_TYPE } from '../constants';

//action
function requestPosts(ApiName) {
  return {
    type: REQUEST_POSTS,
    ApiName
  };
}
//action
function receivePosts(ApiName, json) {
  return {
    type: RECEIVE_POSTS,
    ApiName: ApiName,
    posts: json.Body
  };
}

//requestPostsactionreceivePostsaction
export function fetchPosts(ApiName, header, params) {
  var header = merge({"Content-Type":"application/json"}, header);
  // (cookie)var header = {"Content-Type":"application/json", "code":getCookie("code"), "token":getCookie("token")};
  return dispatch => {
    dispatch(requestPosts(ApiName));
    return fetch(getBaseUrl()+ getAPI(ApiName),
      {
        method: 'POST',
        headers: header,
        body:  JSON.stringify(params)
      })
      .then(response => response.json())
      .then(json => dispatch(receivePosts(ApiName, json)));
  };
}
//requestPostsactionreceivePostsaction
export function fetchGET(ApiName, params) {
  var header = {"Content-Type":"application/json", "code":params.code, "token":params.token};
  return dispatch => {
    dispatch(requestPosts(ApiName));
    return fetch(getBaseUrl()+ getAPI(ApiName),
      {
        method: 'GET',
        headers: header
      })
      .then(response => response.json())
      .then(json => dispatch(receivePosts(ApiName, json)));
  };
}

export function fetchGETParams(ApiName, params) {
  var header = {"Content-Type":"application/json"};
  return dispatch => {
    dispatch(requestPosts(ApiName));
    return fetch("http://ppapi.ppjijin.com/ppapi/AD/InvitorInfo?userId="+params.userid,
      {
        method: 'GET',
        headers: header
      })
      .then(response => response.json())
      .then(json => dispatch(receivePosts(ApiName, json)));
  };
}

//
export function _submitForm(ApiName, params) {
  var header = {"Content-Type":"application/json", "code":getRandomCode()};
    return dispatch => {
      dispatch(fetchPostsByForm(ApiName, header, params));
  };
}
//
export function show_error(ApiName, params) {
  return {
    type: SHOW_ERROR,
    ApiName: ApiName,
    posts:params
  };
}

export function reset(ApiName) {
  return {
    type: RESET,
    ApiName
  };
}


//action
function requestForm(ApiName) {
  return {
    type: REQUEST_FORM,
    ApiName
  };
}


//action
function receiveForm(ApiName, header, json) {
  if(json.Code !== 0){
    return{
      type:SHOW_ERROR,
      ApiName:ApiName,
      posts:json
    };
  }
  return {
      type: RECEIVE_FORM,
      ApiName:ApiName,
      posts:json,
      header:header
  };


}

export function fetchPostsByForm(ApiName, header, params) {
  // fetchreqwest
  return dispatch => {
    dispatch(requestForm(ApiName));
    return fetch(getBaseUrl()+ getAPI(ApiName),
      {
        method: 'POST',
        headers: header,
        body:  JSON.stringify(params)
      })
      .then(response => response.json())
      .then(json => dispatch(receiveForm(ApiName, header, json)));
  };

}

//,,import * as actions actionsexport
