// type
export function changeType(value){
  return{
    type:CHANGE_TYPE,
    value
  }
}
export function selectPaytypelog(name, value){
  return{
    type:SELECT_PAY_TYPE_LOG,
    name,
    value
  }
}
export function selectPaytype(name, value){
  return{
    type:SELECT_PAY_TYPE,
    name,
    value
  }
}




