import 'babel-polyfill'
import "../public/css/pp2015.css"
import React from 'react'
import { render } from 'react-dom'
import { Router, Route, hashHistory } from 'react-router'
import { Provider } from 'react-redux'

import contribute from './containers/contribute' //
import Protocol from './containers/Protocol' //
import myPlatform from './containers/myPlatform' //
import myDreg from './containers/myDreg' //
import platform from './containers/platform'// 
import dregform from './containers/dregform'// 
import buyProtocol from './containers/buyProtocol' // 
import NoticesDetail from './containers/NoticesDetail' // 
import Share from './containers/Share' // 
import download from './containers/download' // 
import ProductBuyLog from './containers/ProductBuyLog'// 
import trade from './containers/trade'// 
import introduction from './containers/introduction'// 


import configureStore from './stores/configureStore'

const store = configureStore();
render(
  <Provider store={store}>
    <Router history={hashHistory}  >
	    <Route path="/contribute" component={contribute} />
	    <Route path="/Protocol" component={Protocol} />
	    <Route path="/buyProtocol" component={buyProtocol} />
	    <Route path="/myPlatform" component={myPlatform} />
	    <Route path="/myDreg" component={myDreg} />
	    <Route path="/platform" component={platform} />
	    <Route path="/dregform" component={dregform} />
	    <Route path="/NoticesDetail" component={NoticesDetail} />
	    <Route path="/Share" component={Share} />
	    <Route path="/download" component={download} />
	    <Route path="/ProductBuyLog" component={ProductBuyLog} />
	    <Route path="/trade" component={trade} />
	    <Route path="/introduction" component={introduction} />


    </Router>
    </Provider>,
  document.getElementById('root')
)



